<template>
	<div id="creditshop-detail">
		<public-header title="订单详情"></public-header>
		<div class="content font-14">
			<div class="margin-10 bg-default padding-15">
				<div class="padding-15-b van-hairline--bottom flex-lr">
					<div class="flex">
						<span class="margin-10-r">兑换物品信息</span>
						<van-tag type="danger" v-if="order.goods_type == 'redpacket'">红包</van-tag>
						<van-tag type="primary" v-else-if="order.goods_type == 'credit2'">余额</van-tag>
						<van-tag type="success" v-else-if="order.goods_type == 'goods'">商品</van-tag>
					</div>
					<tempalte v-if="order.status == 1">
						<span class="c-danger">未处理</span>
					</tempalte>
					<template v-else-if="order.status == 2">
						<span class="c-primary">已处理</span>
					</template>
				</div>
				<div class="flex-lr goods-info margin-15-t">
					<div class="thumb">
						<img class="img-100" :src="order.goods_info.thumb" alt="">
					</div>
					<div class="info">
						<div class="flex-lr w-100">
							<span>{{order.goods_info.title}}</span>
							<span>x1</span>
						</div>
						<div class="flex">
							<div>
								<span class="c-danger font-bold">{{order.goods_info.use_credit1}}积分</span>
								<template v-if="order.goods_info.use_credit2 > 0">
									+ <span class="c-danger font-bold">¥{{order.goods_info.use_credit2}}</span>
								</template>
							</div>
							<span class="c-gray line-through font-12 margin-10-l">{{order.goods_info.old_price}}</span>
						</div>
					</div>
				</div>
			</div>
			<div class="padding-15 bg-default margin-10" v-if="order.goods_type == 'goods'">
				<ul class="flex-lr padding-15-b van-hairline--bottom">
					<li>收件人姓名</li>
					<li>{{order.username}}</li>
				</ul>
				<ul class="flex-lr padding-15-t">
					<li>联系方式</li>
					<li>{{order.mobile}}</li>
				</ul>
			</div>
			<div class="padding-15 bg-default margin-10">
				<ul class="flex-lr padding-15-b van-hairline--bottom">
					<li>订单号</li>
					<li>{{order.order_sn}}</li>
				</ul>
				<ul class="flex-lr padding-15-tb van-hairline--bottom">
					<li>
						消耗积分<template v-if="order.use_credit2 > 0">+消耗余额</template>
					</li>
					<li>
						{{order.use_credit1}}积分
						<template v-if="order.use_credit2 > 0">+ ¥{{order.use_credit2}}</template>
					</li>
				</ul>
				<ul class="flex-lr padding-15-t">
					<li>兑换时间</li>
					<li>{{order.addtime}}</li>
				</ul>
			</div>
			<div class="margin-10" v-if="order.status == 1 && code">
				<van-button size="normal" class="font-15" :class="{'bg-primary' : islegal}" block :disabled="!islegal" @click="onCodeConfirm">确认核销 (核销码：{{code}})</van-button>
			</div>
		</div>
		<iloading v-if="showPreLoading"></iloading>
	</div>
</template>

<script>
	import publicHeader from "@/components/header"
	import {getData, setData} from "@/controller/funcCommon"
	export default {
		components: {
			publicHeader,
		},
		data() {
			return {
				id: 0,
				code: '',
				order: {
					goods_info: {}
				},
				showPreLoading: true,
				islegal: false
			}
		},
		methods: {
			onLoad() {
				var that = this;
				getData({
					vue: that,
					url: 'plateform/creditshop/order/detail',
					data: {
						id: that.id
					},
					autoAssign: true,
					variable: 'order',
					success: (res) => {
						that.islegal = true;
					}
				})
			},
			onCodeConfirm() {
				var that = this;
				if(!that.islegal) {
					return;
				}
				that.islegal = false;
				setData({
					vue: that,
					url: 'plateform/creditshop/order/confirm',
					data: {
						code: that.code
					},
					redirect: 'refresh',
					message: '核销成功',
					confirm: '确定核销该订单吗?',
					fail: (res) => {
						that.util.$toast(res);
						that.islegal = true;
					}
				})
			}
		},
		mounted() {
			let query = this.util.parseQuery(this.$route.query);
			this.id = query.id;
			this.code = query.code;
			this.onLoad();
		}
	}
</script>

<style>
	#creditshop-detail .goods-info .thumb{
		width: 50px;
		height: 50px;
		border-radius: 3px;
		overflow: hidden;
		font-size: 0px;
	}
	#creditshop-detail .goods-info .info{
		height: 50px;
		font-size: 14px;
		flex: 1;
		padding-left: 10px;
		display: flex;
		flex-flow: column nowrap;
		align-items: flex-start;
		justify-content: space-between;
	}
</style>


